<template>
  <div class="ticket-purchase-page">
    <!--  1      -->
    <LogoHeader />

    <!--  ,    -->
    <div class="menu">
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/ticket/info' }"
        @click="navigateTo('/ticket/info')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/ticket/purchase' }"
        @click="navigateTo('/ticket/purchase')"> </div>
    </div>

    <!--     -->
    <div class="ticket-purchase-content">
      <div  class="ticket-purchase-layout">
      <div class="info-box">
        <div class="icon">
          <img src="@/assets/img/anyImg/bell-icon.png" alt="alert" />
        </div>
        <div class="info-content">
          <h4>    !</h4>
          <ul>
            <li> -    7  13 . (GS MMS:  7  13)</li>
            <li> -          ,             .</li>
            <li> - PC   .</li>
            <li> - 1  4  .</li>
          </ul>
          <p>  [TICKET] - []       .
          </p>
        </div>
      </div>

      <!--    -->
      <div>
        <table class="ticket-purchase">
          <thead>
            <tr>
              <th> </th>
              <th> </th>
              <th></th>
              <th> </th>
              <th> </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="match in matches" :key="match.matchId">
              <td> {{ formatDate(match.matchDate) }}
                2024.10.24()<br>
                19:00
              </td>
              <td> {{ formatMatchInfo(match.matchTeam, thisTeam) }} GS MMS VS </td>
              <td> {{ match.matchStadium }}  </td>
              <td>
                <p>[]</p>
                <p>{{ formatDate(match.reservationDate1) }}</p>
                <p>[]</p>
                <p>{{ formatDate(match.reservationDate2) }}</p>
              </td>
              <!--        -->
              <td>
                <button class="ticket-Modal" @click="openModal(match.matchId)"></button>
                <!--   -->
                <Modal v-if="isModalVisible" :visible="isModalVisible" @close="closeModal" />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import LogoHeader from '../common/LogoHeader.vue';
import Modal from './TicketModal.vue';

export default {
  name: 'TicketPurchase',
  components: {
    LogoHeader,
    Modal,
  },
  props: {
    user : {
      type: Object,
      default: () => ({ name: '', role: 'guest', email: '', isLoggedIn: false})
    },
  },
  async mounted(){
    this.fetchEvents();
  },
  data() {
    return {
      activeMenu: this.$route.path, //   
      thisTeam : "GS ITM",
      isModalVisible: false, //   
      //    
      matches : [], 
      //ticket modal   
      matchId : 0,
    };
  },
  watch: {
    //    activeMenu 
    $route(to) {
      this.activeMenu = to.path;
    }
  },
  // created() {
  //     //    API  
  //     this.fetchMatches();
  // },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
      this.activeMenu = route; //      
    },
    //    
    async fetchEvents(){
      try {
        const response = await axios.get('/ticket/purchase');
        console.log(response.data);
        this.matches = response.data; //   
        console.log("Matches fetched: ", this.matches);
      } catch (error) {
        console.log("Error fetching matches:", error);
      }
    },

    //     
    formatMatchInfo(matchTeam, thisTeam) {
      return matchTeam === "" ? `${thisTeam} VS ${matchTeam}` : `${matchTeam} VS ${thisTeam}`;
    },
    //   
    formatDate(date) {
        const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
        const dateObj = new Date(date);
        
        //  
        const formattedDate = dateObj.toLocaleDateString('ko-KR', options);
        
        //  
        const days = ['', '', '', '', '', '', ''];
        const dayName = days[dateObj.getDay()];

        //  
        const formattedTime = dateObj.toLocaleTimeString('ko-KR', {
          hour: '2-digit',
          minute: '2-digit'
        });

        return `${formattedDate}(${dayName})<br>${formattedTime}`;
      }
    },
    //   
    openModal(matchId) {
      this.isModalVisible = true;
      axios.get(`/ticket/purchase/modal/${matchId}`)
        .then((response) => {
          console.log("Reservation successful:", response);
          //   
        })
        .catch((error) => {
          console.error("Error making reservation:", error);
        });
    },
    closeModal() {
      this.isModalVisible = false;
    },
}
</script>

<style scoped>

/*  */
