  align-items: center;
  width: 100%;
  max-width: 1400px;
}

.ticket-purchase {
  width: 100%;
  /*     */
  border-collapse: collapse;
  /*   */
  font-size: 20px;
  /*    */
  background-color: #f9f9f9;
  /*    */
  justify-content: center;
  /*     */
  align-items: center;
  /*     */
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}

th, td {
