  text-align: center;
  /*    */
  padding: 12px 5px;
  /* ,  12px / ,  5px */
}

th {
  background-color: #f2f2f2;
  /*    */
  color: #333;
  /*    */
  font-weight: bold;
  /*    */
}


.ticket-Modal {
  background-color: #4CAF50;
  /*   () */

  border: 1px;
