<template>
    <!--   -->
    <div id="modal" class="modal" v-if="visible">

        <!--   -->
        <div class="first-modal-content">

            <!--   -->
            <!--      -->
            <div class="modal-title" v-if="firstPage">
                <h2>{{ formattedMatchInfo }}GS MMS VS </h2>
                <div>
                    {{ match.matchStadium }} | {{ formattedMatchDate }}
                </div>
                <hr class="divider" />
            </div>


            <!--   -->
            <div v-if="firstPage">

                <div class="modal-body">
                    <!--   -->
                    <div class="modal-ticket-img" v-if="selectedZoneImage">
                        <img :src="selectedZoneImage" alt="  " class="stadium-image" />
                    </div>

                    <!--   -->
                    <div class="modal-table-container">
                        <table class="modal-table">
                            <thead class="table-theader">
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>

                            <!-- DB     -->
                            <tbody>
                                <tr v-for="zone in seatList" :key="zone.zoneName">
                                    <td class="zone-name">
                                        <input type="checkbox" class="select-zone-btn" v-model="isChecked"
                                            @change="onCheckboxChange">
                                            {{ zone.zoneName }} [GOLD ZONE]
                                    </td>
                                    <td>
                                    </td>
                                    <td class="available-seat-zone">{{ zone.availableSeatAmount }}</td>
                                </tr>
                            </tbody>
                        </table>

                        <!--   -->
                        <table v-if="isChecked" class="modal-table-hide">
                            <tbody>

                                <tr v-for="section in zone.sections" :key="section.seatId" class="section">
                                    <td class="section-name">{{ section.sectionName }}</td>
                                    <td>
                                        <div class="quantity-selector">
                                            <button @click="decreaseQuantity(index)"
                                                :disabled="zone.quantity <= 0 || (activeButtonIndex !== null && activeButtonIndex !== index)">
                                                -
                                            </button>
                                            <input type="text" v-model.number="selectedSeats[section.seatId]" :max="section.availableSeatAmount" min="0" readonly class="quantity-box" />
                                            <button @click="increaseQuantity(index)"
                                                :disabled="zone.quantity >= zone.maxSeats || (activeButtonIndex !== null && activeButtonIndex !== index)">
                                                +
                                            </button>
                                        </div>
                                    </td>
                                    <td class="available-seat-section">{{ section.availableSeatAmount }}{{ zone.fullSeats }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <hr class="divider" />

                <div class="result-text">
                        .
                    <div v-for="zone in selectedZones" :key="zone.name">
                          : {{ zone.name }} {{ zone.quantity }}
                    </div>
                    <div class="result-text-all">
                         : {{ countTicket.count }}
                    </div>
                </div>
                <div class="first-button">
                    <!--   -->
                    <button class="buy-button" @click="openSecondPage"></button>

                    <!--   -->
                    <button class="close-button" @click="closeModal"></button>
                </div>

            </div>
            <!-- ------------------------------------------------------------------ -->

            <!-- 2   -->
            <div v-if="secondPage" class="second-modal-content">
                <div class="modal-body">

                    <!--   -->
                    <div class="left-table">
                        <div class="modal-table-container">
                            <table class="modal-table">
                                <tr>{{ selectedZone }} {{ ticketAmount }} .</tr>
                                <thead class="table-theader">
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                </thead>

                                <!-- DB     -->
                                <tbody>
                                    <tr v-for="zone in selectedZones" :key="zone.name">
                                        <td> {{ zone.name }} </td>
                                        <td> {{ zone.quantity }}</td>
                                        <td> {{ zone.price }}</td>
                                        <td>
                                            <p>{{ membershipName }}</p>
                                            <p>{{ membershipDiscount }}</p>%
                                        </td>
                                    </tr>
                                </tbody>
                            </table>

                            <table class="modal-table">
                                <thead class="table-theader">
                                    <tr>
                                        <td colspan="4">  </td>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td colspan="4"><p>{{ calculatedPaymentAmount }}</p> </td>
                                    </tr>
                                </tbody>
                            </table>

                            <div class="modal-info-box">
                                <div class="icon">
                                    <img src="@/assets/img/anyImg/bell-icon.png" alt="alert" />
                                </div>
                                <div class="info-content">
                                    <h4> </h4>
                                    <ul>
                                        <li> -     1  </li>
                                        <li> -           .</li>
                                    </ul>
                                    <h4> </h4>
                                    <p class="p">   12  
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="vertical-divider"></div>

                    <!--   -->
                    <div class="right-table">

                        <!--   -->
                        <!--      -->
                        <div class="modal-second-title">
                            <h2>{{ formattedMatchInfo }}</h2>
                            <div>
                                {{ match.matchStadium }} | {{ formattedMatchDate }}
                            </div>
                        </div>
                        <div>
                            
                        </div>
                        <div v-for="zone in selectedZones" :key="zone.name">
                            {{ zone.name }} / {{ zone.quantity }}
                        </div>
                        <hr class="divider" />

                        <div>
                            <table class="second-modal-table">
                                <tr>
                                    <td> </td>
                                    <td>
                                        <p>{{ calculatedTicketTotalPrice }}</p> 
                                    </td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td>
                                        <p>{{ membershipDiscount }}</p>%
                                    </td>
                                </tr>
                                <tr>
                                    <td>  </td>
                                    <td>
                                        <p>{{ calculatedPaymentAmount }}</p>
                                    </td>
                                </tr>
                            </table>
                            <br>
                            <table class="second-modal-table">
                                <tr>
                                    <td> </td>
                                    <td><p>{{ userBalance }}</p></td>
                                </tr>
                                <tr>
                                    <td> -   </td>
                                    <td><p>{{ calculatedPaymentAmount }}</p></td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td><p>{{ remainingBalance }}</p></td>
                                </tr>
                            </table>

                        </div>
                        <div>
                            <p class="p">
                                 :  [12 ]<br>
                                <!--  -->
                                 : []
                            </p>
                        </div>
                        <div class="second-button">
                            <!--   -->
                            <button class="buy-button" @click="makeReservation"></button>
                            <!--   -->
                            <button class="close-button" @click="openFirstPage"></button>
                        </div>
                    </div>


                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'TicketModal',
    components: {
    },
    props: {
        visible: {
            type: Boolean,
            default: false,
        },
        matchId : {
            type : String,
            required : true
        }
    },

    computed: {
        selectedZones() {
            //  1   
            return this.zones.filter(zone => zone.quantity > 0);
        },

        formattedTotal() {
            //       
            return this.total.toLocaleString();
        },
        membershipName() {
            return this.userMembership.membershipType;
        },
        membershipDiscount() {
            return this.userMembership.membershipDiscount;
        },
        formattedMatchDate() {
            const date = new Date(this.match.matchDate);
            const days = ['', '', '', '', '', '', ''];
            const dayName = days[date.getDay()];
            return `${date.toLocaleDateString('ko-KR')}(${dayName}) ${date.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' })}`;
        },
        formattedMatchInfo() {
            return this.match.matchTeam === ''
                ? `${this.thisTeam} VS ${this.match.matchTeam}`
                : `${this.match.matchTeam} VS ${this.thisTeam}`;
        },
        calculatedPaymentAmount() {
            const discountRate = (100 - this.membershipDiscount) * 0.01;
            return this.ticketPrice * this.ticketAmount * discountRate;
        },
        calculatedTicketTotalPrice() {
            return this.ticketPrice * this.ticketAmount;
        },
        remainingBalance() {
            return this.userBalance - this.calculatedPaymentAmount;
        }
    },

    watch: {
        //    ,   0 activeButtonIndex 
        zones: {
            handler() {
                if (this.zones.every(zone => zone.quantity === 0)) {
                    this.activeButtonIndex = null;
                }
            },
            deep: true
        }
    },

    data() {
        return {
            thisTeam: 'GS ITM', //   

            //  
            // zones: [
            //     { name: 'GA', quantity: 0, maxSeats: 4, fullSeats: '30', imageUrl: 'stadium-GA.jpg', price: 30000 },
            //     { name: 'GB', quantity: 0, maxSeats: 4, fullSeats: '30', imageUrl: 'stadium-GB.jpg', price: 30000 },
            //     { name: 'GC', quantity: 0, maxSeats: 4, fullSeats: '30', imageUrl: 'stadium-GC.jpg', price: 30000 }
            // ],

            activeButtonIndex: null, //     

            isChecked: false, //   

            //  
            countTicket: {
                count: 0,
            },

            //    URL
            selectedZoneImage:
                require('@/assets/img/stadium/stadium-main.jpg'),

            //   
            firstPage: true,
            secondPage: false,

            total: 0,
            // api  
            match: {
                matchDate: '',
                matchTeam: '',
                matchStadium: ''
            },
            ticketSalesDTO: {},
            userBalance: {
                userId : 0,
                paymentAmount : 0,
                membershipPrice : 0,
                ticketPaidPrice : 0,
                leftMoney: 0
            }, //   
            userMembership: {
                membershipType: '24/25',
                membershipDiscount: 30
            },
            //     
            selectedZone: '', //   
            ticketPrice: 0,
            ticketAmount: 2, //   
        };
    },
    created() {
        this.fetchMatchData();
    },
    methods: {
        fetchMatchData() {
            axios.get(`/purchase/modal/${this.matchId}`)
                .then((response) => {
                    const data = response.data;
                    this.match = data.matchInfo;
                    this.seatList = data.availableSeatList;
                    this.ticketSalesDTO = data.ticketSalesDTO;
                    this.userMembership = data.userMembership;
                    this.userBalance = data.userBalance;
            })
            .catch((error) => {
                console.error("Error fetching match data:", error);
            });
        },
        makeReservation() {
            axios.post('/purchase/payment/completed', this.ticketSalesDTO)
                .then((response) => {
                    console.log("Reservation successful:", response);
                    this.$router.push('/myPage/ticket');
                })
                .catch((error) => {
                    console.error("Error during reservation:", error);
            });
        },

        // 
        calculateTotal() {
            for (let i = 0; i < this.selectedZones.length; i++) {
                this.total = this.selectedZones[i].price * this.selectedZones[i].quantity + this.total;
            }
        },

        //   
        openFirstPage() {
            this.firstPage = true;
            this.secondPage = false;
            this.total = 0;
        },

        //   
        openSecondPage() {
            this.calculateTotal();
            if (this.total > 0) {
                this.firstPage = false;
                this.secondPage = true;
            } else {
                alert(" .");
            }
        },

        onCheckboxChange() {
            if (this.isChecked) {
                this.showDetails();
            } else {
                this.hideDetails();
            }
        },
        showDetails() {
        },
        hideDetails() {
            this.countTicket.count = 0;
            this.zones.forEach(zone => {
                zone.quantity = 0; //    0                 
            });
            this.selectedZoneImage =
                require('@/assets/img/stadium/stadium-main.jpg'); //  
        },

        //   
        closeModal() {
            this.hideDetails();
            this.isChecked = false;
            this.$emit('close');
        },
        openModal() {
            this.isModalOpen = true;
        },
        //  
        increaseQuantity(index) {
            if (this.zones[index].quantity < this.zones[index].maxSeats) {
                this.zones[index].quantity++;
                this.activeButtonIndex = index; //     
                this.countTicket.count++;
                this.selectedZoneImage = require(`@/assets/img/stadium/${this.zones[index].imageUrl}`);

            }
        },
        decreaseQuantity(index) {
            if (this.zones[index].quantity > 0) {
                this.zones[index].quantity--;
                this.activeButtonIndex = index; //     
                this.countTicket.count--;
                this.selectedZoneImage = require(`@/assets/img/stadium/${this.zones[index].imageUrl}`);

            }
        },

        selectZone(index) {
            //    URL 
            this.selectedZoneImage = this.zones[index].imageUrl;
        },

    }
}
</script>


<style scoped>
