    margin-top: 10px;
    /*   */
}

.right-table {
    margin-left: 10%;
    width: 400px;
}

.left-table {
    margin-top: 100px;
}

.second-modal-table {
    width: 400px;
    margin-left: auto;
    margin-right: auto;
